<template>
    <h1>Get an Email</h1>
    <input type="email" placeholder="From:" v-model="email" required/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getMail">Confirm</button>
</template>

<script>

var validator = require("email-validator");

export default {
    name: "MailA",
    data() {
        return {
            email: null
        }
    },
    methods: {
        getMail() {
            if (validator.validate(this.email)) {
                let A = {
                    "object": this.email
                }
                let Action = {
                    "content": JSON.stringify(A),
                    "id": "633856703a79d71704729438"
                }
                let nameAct = {
                    "name": "Gmail",
                    "description": "Get an email"
                }
                localStorage.action = JSON.stringify(Action);
                localStorage.nameact = JSON.stringify(nameAct);
                var myHeaders = new Headers();
                myHeaders.append("token", localStorage.token);
                var requestOptions = {
                  method: 'GET',
                  headers: myHeaders,
                  redirect: 'follow'
                };
                fetch(localStorage.server + "/user/gmail/verify", requestOptions)
                    .then(res => {
                        res.json().then((ans) => {
                            if (ans["status"] == "ok") {
                              this.$router.push({ name: 'Recap' });
                            }
                            else {
                              localStorage.path = 'Recap';
                            
                              var requestOptions = {
                                method: 'GET',
                                headers: myHeaders,
                                redirect: 'follow'
                              };
                          
                              fetch(localStorage.server + "/user/gmail/getLink/?scope=https://www.googleapis.com/auth/gmail.send", requestOptions)
                                .then(rep => {
                                    if (rep.status === 200) {
                                        rep.text().then((ans) => {
                                            window.location.href = ans;
                                        })
                                    }
                                })
                                .catch(error => console.log('error', error));
                            }
                        })
                    })
                  .catch(error => console.log('error', error));
            } else {
                alert('Email is not valid')
            }
        },
    }
}
</script>

